import { useEffect } from "react";
import PageHeaderComponent from "../components/PageHeaderComponent";
import ServiceComponent from "../components/ServiceComponent";
import { HiOutlineGlobeAlt }  from "react-icons/hi"
import services from "../data/services.json";
import "../styles/Services.css";

const Services = () => {
    useEffect(() => {
        document.title = services.documentTitle;
    }, []);

    return (
        <div className="container">
            <PageHeaderComponent
                pageIntroTitle={services.pageIntroTitle}
                pageIntroContent={services.pageIntroContent}
            />

            <div className="row">
                {services.servicesList.map((service) => {
                    return (
                        <div className="col-md-4">
                            <ServiceComponent key={service.id} icon={service.icon} title={service.title} description={service.shortDescription} />
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default Services;
